.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: silver;
    height:50px;
}

.toolbar a{
    color:white;
    text-decoration: none;
    font-size: 2rem;
    padding-top: 3px;
    padding-left: 5px;
    display:flex;
    font-family: "STKaiti","DFKai-SB";
    float:left;
}

.toolbar button{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height:50px;
    width:50px;
    padding-top: 13px;
    padding-bottom: 15px;
    background:transparent;
    border:none;
    display:flex;
    float:right;
}
.toolbar button:focus{
    outline: none;
}
.button__line{
    width: 30px;
    height: 2px;
    background: white;
}
